	module.exports = {"data":{"markdownRemark":{"html":"<p>Create usable content? It doesn't sound like a difficult goal. But upon reconsideration, it is clear that the web (in particular) has become a waste of  your time. The visual aspects of the internet are all distractions without quality content. And that means content that is useful and clear. </p>\n<p>Is your website full of unclear, inconsistent, out-of-date, unrevised content? It's out of style; let's all move on together now as one happy sector. </p>\n<p>So three cheers for web designer <a href=\"http://www.7nights.com/asterisk/\">Keith Robinson's </a> focus this week on content.\nHere's a great sample from his post today: </p>\n<blockquote class=\"large\">\n<strong>What Usable Content Is</strong>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">* Usable content is clear and easy to understand.\n* Usable content can bridge gaps: things like language barriers, disability and cultural differences.\n* Usable content is meaningful.\n* Usable content makes the reader feel smart.\n* Usable content is goal and audience appropriate.\n* It&#39;s thought-out, planned and constantly maintained.\n* It&#39;s fresh, light and lively.\n* It&#39;s content that is organized in a way that people understand and can get their mind around.\n* It&#39;s designed to be accessible.\n* It&#39;s reusable and shareable, readily available and easy to locate.\n* It&#39;s straightforward, open and honest and to the point.\n* It encourages feedback and is engineered for conversation.\n* It&#39;s hard work, but worthy of the job.</code></pre>\n      </div>\n<p><strong>What Usable Content Is Not</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">* Usable content is not clever, obtuse or misleading.\n* It&#39;s not marketing drivel, or bland branding messages.\n* It&#39;s not longwinded.\n* It&#39;s not written at the highest possible reading level.\n* It&#39;s doesn&#39;t use &#39;big words&#39; unless they are needed.\n* It&#39;s not legalese.\n* It&#39;s not double talk.\n* It&#39;s not an afterthought.\n* It&#39;s not a mission statement.\n* It&#39;s not an org chart.\n* It&#39;s not self-centered.</code></pre>\n      </div>\n</blockquote>","frontmatter":{"created_at":"April 14, 2005","path":"/2005/04/14/a-few-words-against-drivel-and-detritus","title":"A Few Words Against Drivel and Detritus","tags":["usability","content"]}}},"pathContext":{}}

